
import VHeader from '@/components/v-header/v-header'
import SearchHeader from '@/components/seach-header/seach-header'
import Scroll from '@/components/scroll/scroll'
import Slider from '@/components/slider/slider'
import IndexList from '@/components/index-list/index-list'
import Range from '@/components/range/range'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'


import axios from '@/api/axiosApi'
import bannerList from '@/assets/json/bannerAddress.json'
import icon from '@/assets/json/addressIcon.json'
import { isIphoneX } from '@/assets/js/brower'

export default {
name: 'strategyList',
data() {
return {
icon,
bannerList,
headerOpacity: 0,
tagWidth: 350,
showSearch: false,
seachValue: '',
searchList: [],
hotTags: [{
id: '',
name: '全部'
}],
currentTag: '',
strategList: [],
filter: {
between: [0, 50000],
days: '~'
},
tempFilter: {
between: [0, 50000],
days: '~'
},
fitCount: 0,  // 符合条件的数量
otherSort: {
value: 'readCount',
type: 'DESC',
label: '综合排序'
},
sortType: [
{
value: 'readCount',
type: 'DESC',
label: '综合排序'
},
{
value: 'price',
type: 'ASC',
label: '价格低→高'
},
{
value: 'price',
type: 'DESC',
label: '价格高→低'
},
{
value: 'days',
type: 'ASC',
label: '出行天数低→高'
},
{
value: 'days',
type: 'DESC',
label: '出行天数高→低'
}
],
daysList: [
{
value: '~1',
label: '一日游'
},
{
value: '2~3',
label: '1~3天'
},
{
value: '4~7',
label: '4~7天'
},
{
value: '8~',
label: '7天以上'
}
],
showSort: false,
showFilter: false
}
},
directives: {
TransferDom
},
components: {
VHeader, Scroll, Popup, Slider, Scroller, IndexList, Range, Checker, CheckerItem, SearchHeader
},
mounted() {
this.$refs.scroll.$el.style.height = (window.innerHeight - this.$refs.scroll.$el.offsetTop) + 'px'
},
activated () {
this._getStrategList()
},
created() {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingBottom = 20
}
this._getBanner()
this._getStrategList()
this._getHotTags()
},
methods: {
_getBanner() {
axios.get(this, '/v1/banner/4', null, (data) => {
data.forEach(item => {
item.thumbUrl += '-banner'
})
this.bannerList = data
}, null, false)
},
_getStrategList() {
const _self = this
axios.get(this, `/v1/tourismStrategyExtra`, {
tourismId: this.$route.params.strategyId,
sortName: this.otherSort.value,
sortType: this.otherSort.type,
minPrice: this.filter.between[0],
maxPrice: this.filter.between[1] >= 50000 ? '' : this.filter.between[1],
minDays: this.filter.days.split('~')[0],
maxDays: this.filter.days.split('~')[1],
tagIds: this.currentTag
}, (data) => {
_self.strategList = data
})
},
_getHotTags() {
axios.get(this, `/v1/tag`, {
type: 6,
pageSize: 12
}, (data) => {
data.splice(0, 0, {
id: '',
name: '全部'
})
this.hotTags = data
if (data.length > 0) {
this.currentTag = data[0].id
}
setTimeout(() => {
let w = 0
for (let i = 0; i < this.$refs.tagWrapper.children.length; i++) {
const item = this.$refs.tagWrapper.children[i]
w += item.offsetWidth + 12
}
this.tagWidth = w
}, 50)
})
},
_getStrategyNum() {
axios.get(this, `/v1/tourismStrategyExtraCount`, {
tourismId: this.$route.params.strategyId,
minDays: this.tempFilter.days.split('~')[0],
maxDays: this.tempFilter.days.split('~')[1],
minPrice: this.tempFilter.between[0],
maxPrice: this.tempFilter.between[1] >= 50000 ? '' : this.tempFilter.between[1]
}, (data) => {
this.fitCount = data
})
},
scroll(position) {
if (position.y <= -128) {
this.headerOpacity = 1
} else {
this.headerOpacity = Math.abs(position.y / 188)
}
},
clickTag(tagId) {
this.currentTag = tagId
this._getStrategList()
},
clickItem(contentId, name, id) {
if (contentId) {
if (id != null) {
axios.put(this, `/v1/tourismStrategyExtra/readCount/${id}`)
}
this.$router.push(`/content/${contentId}/${name}`)
}
},
clickSortType(sortItem) {
this.otherSort = sortItem
this._getStrategList()
this.showSort = false
},
filterPriceResult() {
this.filter = this.tempFilter
this._getStrategList()
this.showFilter = false
},
toShowFilter() {
this.showFilter = true
this._getStrategyNum()
this.resetFilter()
},
resetFilter() {

this.tempFilter = {
between: this.filter.between,
days: this.filter.days
}
},
priceChange(val) {
this.tempFilter.between = val
this._getStrategyNum()
},
submitSearch (value) {
axios.get(this, `/v1/tourismStrategyExtra`, {
tourismId: this.$route.params.strategyId,
name: value,
pageSize: 10
}, (data) => {
this.searchList = data
})
},
clickSearchItem (item) {
this.clickItem(item.contentId, item.name, item.id)
this.showSearch = false
this.seachValue = ''
this.searchList = []
}
}
}



